.img-thumbnail{
    padding: 0px 16px;
    border: none; 
    border-radius: 0%;
}
.product-box{
    border:solid 1px rgba(112,112,112,.38);;
    margin:0px 0px 0px 0px;
    padding: 16px 0;
    background-color: #fff;
}
.product__name{
    color: #151b39;
    font-size: 14px;
    font-weight: bold;
    line-height: 20px;
}
.product__info{
    color: gray;
    font-size: 12px;
    line-height: 20px;
}

.product__info2{
    color: red;
    font-size: 12px;
    line-height: 20px;
}

