/* Material Modal */


/* Material Input */
.materialInput{
    width: 100%;
    min-width: 200px;
    height: 30px;
    border-bottom: 2px solid #2874f0;
    position: relative;
    /* background-color: red; */
    margin-top: 25px;
  }
  .materialInput input{
    width: 100%;
    flex: 1;
    outline: none;
    border: none;
    z-index: 1;
    background: transparent;
    line-height: 30px;
  }
  .materialInput .label{
    position: absolute;
    line-height: 30px;
    color: grey;
    z-index: 0;
    font-size: 12px;
    letter-spacing: 1px;
    transition: all 0.1s ease-in-out;
  }
  .materialInput .label.focus{
    top: -20px !important;
  }

  /* Material Button */
.materialButton{
    width: 100%;
    display: inline-block;
    padding: 15px;
    font-size: 15px;
    outline: none;
    background-color: #fb641b;
    color: #ffffff;
    box-shadow: 0 1px 2px 0 rgba(0,0,0,.2);
    border: none;
    cursor: pointer;
    border-radius: 2px;
}
.materialButton:hover{
    box-shadow: 0 4px 6px 0 rgba(0,0,0,.12);
}

/* Material dropdown */


  .anchorButton{
    background: transparent;
    border: none;
    letter-spacing: 1px;
  }

  .breed li{
    list-style: none;
    display: inline;
    
  }
  .breed a{
    color: rgba(128, 128, 128, 0.774);
  }


  .breed li a:hover{
    text-decoration: none;
    color: gray;
  }